import Vue from 'vue'
// 需npm install vue-resource --save下载模块
import VueResource from 'vue-resource'
// 需npm install vue-router --save下载模块
import VueRouter from 'vue-router'
import App from './App'
import Routes from './routes'

Vue.config.productionTip = false
Vue.use(VueResource)
Vue.use(VueRouter)

