<!-- 模板 -->
<template>
	<div id="add-blog">
		<h2>添加博客</h2>
		<form v-if="!summited">
			<label>博客标题</label>
			<input type="text" v-model="blog.title" required />
			<label>博客内容</label>
			<textarea v-model="blog.content"></textarea>
			<div id="checkboxes">
				<label>Vue.js</label>
				<input type="checkbox" value="Vue.js" v-model="blog.categories">
				<label>Node.js</label>
				<input type="checkbox" value="Node.js" v-model="blog.categories">
				<label>React.js</label>
				<input type="checkbox" value="React.js" v-model="blog.categories">
				<label>Angular4</label>
				<input type="checkbox" value="Angular4" v-model="blog.categories">
				<label>杂项</label>
				<input type="checkbox" value="杂项" v-model="blog.categories">
			</div>
			<label>作者</label>
			<select v-model="blog.author" class="author">
				<option v-for="author in authors">
					{{author}}
				</option>
			</select>
			<button v-on:click.prevent="post">添加博客</button>
		</form>
		<div v-if="summited">
			<h3>您的博客发送成功</h3>
		</div>
		<hr>
		<div id="preview">
			<h3>博客预览</h3>
			<p>博客标题:{{blog.title}}</p>
			<p>博客内容:</p>
			<p class="textarea2">{{blog.content}}</p>
			<p>博客分类:</p>
			<ul>
				<li v-for="category in blog.categories">
					{{category}}
				</li>
			</ul>
			<p>发布者:{{blog.author}}</p>
		</div>
	</div>
</template>
<!-- Script -->
<script>

export default {
	name: 'add-blog',
	data () {
		return {
			blog:{
				title:"",
				content:"",
				categories:[],
				author:""
			},
			authors:["Caico","Jaime","Messi"],
			summited:false//v-if是根据表达式的值判断一个元素是生成或移除，如果判断为true，就生成元素；否则，移除元素。
		}
	},
	methods:{
		post:function(){
			show.value = show.value + txt.value + "\n";  
			this.$http.post("https://my-vue-data-base1.firebaseio.com/post.json",this.blog)
			.then(data=>console.log(data));
			this.summited=true;
			}
		}
	}
</script>

<style scoped>
#add-blog *{
	box-sizing:border-box;
	text-align: center;
}
#add-blog{
	margin:20px auto;
	max-width:600px;
	padding:20px;
}
label{
	display: block;
	margin: 20px 0 10px;
}
input[type="text"],textarea,select{
	display: block;
	width: 100%;
	padding: 8px;
}
textarea{
	min-width: 600px;
	max-width: 600px;
	min-height: 220px;
	border: 1px solid #444;
}
textarea,.textarea2{
	text-align: left !important;
	text-indent: 2em;
}
ul li{
	list-style-type: none;
	display: inline-block;
	margin:0 5px;
}
#checkboxes label{
	display: inline-block;
	margin-top: 0;
}
#checkboxes input{
	display: inline-block;
	padding:0 5px;
}
select.author{
	width: 100px;
	margin: 0 auto;
}
button{
	display: block;
	margin: 20px auto;
	background:crimson;
	color: #fff;
	border: 0;
	padding: 14px;
	border-radius: 4px;
	font-size: 18px;
	cursor: pointer;
}
#preview{
	padding: 10px 20px;
	border: 1px dotted #ccc;
	margin: 30px 0;
}
h3{
	margin-top: 10px;
}
</style>



