<template>
  <div id="single-blog">
    <h1>{{blog.title}}</h1>
    <div class="details">
      <span class="left">发布者:{{blog.author}}</span>
      <span class="right">关键字:
        <ul v-for="category in blog.categories">
          <li>{{category}}</li>
        </ul>
      </span>
    </div>
    <hr>
    <div class="content" style="word-break:break-all;word-wrap:break-word;">{{blog.content}}</div>
  </div>
</template>

<script>
  export default {
    name: "single-blog",
    data() {
      return {
        id: this.$route.params.id,
        blog: {}
      }
    },
    created() {
      this.$http.get('https://my-vue-data-base1.firebaseio.com/post/' + this.id + '.json')
        .then(data => {
          return data.json();
        })
        .then(data => {
          console.log(data);
          this.blog = data;
        })
    }
  };
</script>

<style scoped>
  #single-blog {
    max-width: 960px;
    margin: 0 auto;
    padding: 20px;
    background: #eeeeee;
    border: 1px dotted #aaaaaa;
  }
  .left {
    folat: left;
    padding: 0 10px;
  }
  .details{
    overflow: hidden;
    height: 30px;
    line-height: 30px;
  }
  .right {
    float: right;
    padding: 0 10px;
  }
  span{
    display: inline-block;
  }
  ul{
    list-style-type: none;
    display: inline-block;
    margin: auto 1px;
  }
  li {
    display: inline-block
  }
  .content{
    margin-top: 20px;
    text-indent: 2em;
  }
</style>





